import { useState } from 'react';
import {
  Box,
  Container,
  Grid,
  Card,
  CardMedia,
  CardContent,
  Typography,
  TextField,
  InputAdornment,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Button,
  Slider,
  Chip,
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import { Link as RouterLink } from 'react-router-dom';
import React from 'react';

const marks = [
  { value: 2010, label: '2010' },
  { value: 2015, label: '2015' },
  { value: 2020, label: '2020' },
  { value: 2024, label: '2024' },
];

const CarListPage = () => {
  const [searchQuery, setSearchQuery] = useState('');
  const [brand, setBrand] = useState('');
  const [yearRange, setYearRange] = useState([2010, 2024]);
  const [priceRange, setPriceRange] = useState([0, 10000000]);

  const handleYearChange = (event: Event, newValue: number | number[]) => {
    setYearRange(newValue as number[]);
  };

  const handlePriceChange = (event: Event, newValue: number | number[]) => {
    setPriceRange(newValue as number[]);
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Grid container spacing={3}>
        {/* Filters Sidebar */}
        <Grid item xs={12} md={3}>
          <Box sx={{ p: 2, bgcolor: 'background.paper', borderRadius: 2 }}>
            <Typography variant="h6" gutterBottom>
              Фильтры
            </Typography>

            <TextField
              fullWidth
              label="Поиск"
              variant="outlined"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              sx={{ mb: 2 }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
            />

            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>Марка</InputLabel>
              <Select
                value={brand}
                label="Марка"
                onChange={(e) => setBrand(e.target.value)}
              >
                <MenuItem value="toyota">Toyota</MenuItem>
                <MenuItem value="honda">Honda</MenuItem>
                <MenuItem value="nissan">Nissan</MenuItem>
                <MenuItem value="mazda">Mazda</MenuItem>
                <MenuItem value="subaru">Subaru</MenuItem>
              </Select>
            </FormControl>

            <Typography gutterBottom>Год выпуска</Typography>
            <Slider
              value={yearRange}
              onChange={handleYearChange}
              valueLabelDisplay="auto"
              min={2010}
              max={2024}
              marks={marks}
              sx={{ mb: 4 }}
            />

            <Typography gutterBottom>Цена (₽)</Typography>
            <Slider
              value={priceRange}
              onChange={handlePriceChange}
              valueLabelDisplay="auto"
              min={0}
              max={10000000}
              step={100000}
              sx={{ mb: 2 }}
            />

            <Button
              variant="contained"
              fullWidth
              onClick={() => {
                setSearchQuery('');
                setBrand('');
                setYearRange([2010, 2024]);
                setPriceRange([0, 10000000]);
              }}
            >
              Сбросить фильтры
            </Button>
          </Box>
        </Grid>

        {/* Car List */}
        <Grid item xs={12} md={9}>
          <Grid container spacing={3}>
            {[1, 2, 3, 4, 5, 6].map((card) => (
              <Grid item key={card} xs={12} sm={6} md={4}>
                <Card
                  component={RouterLink}
                  to={`/cars/${card}`}
                  sx={{
                    height: '100%',
                    display: 'flex',
                    flexDirection: 'column',
                    textDecoration: 'none',
                    transition: 'transform 0.2s',
                    borderRadius: 4,
                    '&:hover': {
                      transform: 'scale(1.02)',
                    },
                  }}
                >
                  <CardMedia
                    component="img"
                    sx={{
                      height: 200,
                      objectFit: 'cover',
                      backgroundColor: 'background.paper',
                      borderTopLeftRadius: 16,
                      borderTopRightRadius: 16,
                    }}
                    image="/gh-pages.asiatransit/images/cars/camry.jpg"
                    alt="Toyota Camry"
                  />
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography gutterBottom variant="h5" component="h2">
                      Toyota Camry
                    </Typography>
                    <Typography variant="body2" color="text.secondary" gutterBottom>
                      2022 • 50,000 км
                    </Typography>
                    <Typography variant="h6" color="primary">
                      2,500,000 ₽
                    </Typography>
                    <Box sx={{ mt: 1 }}>
                      <Chip label="A+ аукцион" color="success" size="small" sx={{ mr: 1 }} />
                      <Chip label="В наличии" color="info" size="small" />
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Grid>
      </Grid>
    </Container>
  );
};

export default CarListPage; 