import { useState } from 'react';
import {
  Box,
  Container,
  Grid,
  Typography,
  Paper,
  Button,
  List,
  ListItem,
  ListItemText,
  Divider,
  Chip,
  TextField,
  Alert,
} from '@mui/material';
import { useParams } from 'react-router-dom';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import React from 'react';

const CarDetailsPage = () => {
  const { id } = useParams();
  const [name, setName] = useState('');
  const [phone, setPhone] = useState('');
  const [email, setEmail] = useState('');

  // Mock data - in a real app, this would come from an API
  const carDetails = {
    id: id,
    brand: 'Toyota',
    model: 'Camry',
    year: 2022,
    mileage: 50000,
    auctionGrade: 'A+',
    priceJPY: 3500000,
    priceRUB: 2500000,
    features: [
      'Автоматическая коробка передач',
      'Кожаный салон',
      'Климат-контроль',
      'Камера заднего вида',
      'Круиз-контроль',
      'LED фары',
    ],
    images: [
      'https://source.unsplash.com/random/800x600?car,japan,1',
      'https://source.unsplash.com/random/800x600?car,japan,2',
      'https://source.unsplash.com/random/800x600?car,japan,3',
    ],
  };

  const calculateTotalPrice = () => {
    // Mock calculation - in a real app, this would be more complex
    const shipping = 150000;
    const customs = 200000;
    return carDetails.priceRUB + shipping + customs;
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Grid container spacing={4}>
        {/* Car Images */}
        <Grid item xs={12} md={8}>
          <Paper sx={{ p: 2, mb: 2, borderRadius: 4 }}>
            <Box
              component="img"
              src="/gh-pages.asiatransit/images/cars/camry.jpg"
              alt={carDetails.model}
              sx={{ width: '100%', height: 'auto', borderRadius: 4 }}
            />
          </Paper>
          <Grid container spacing={2}>
            {[1, 2].map((index) => (
              <Grid item xs={4} key={index}>
                <Paper sx={{ p: 1, borderRadius: 4 }}>
                  <Box
                    component="img"
                    src="/gh-pages.asiatransit/images/cars/camry.jpg"
                    alt={`${carDetails.model} ${index + 2}`}
                    sx={{ width: '100%', height: 'auto', borderRadius: 4 }}
                  />
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Grid>

        {/* Car Details */}
        <Grid item xs={12} md={4}>
          <Paper sx={{ p: 3, mb: 3, borderRadius: 4 }}>
            <Typography variant="h4" gutterBottom>
              {carDetails.brand} {carDetails.model}
            </Typography>
            <Typography variant="h5" color="primary" gutterBottom>
              {carDetails.priceRUB.toLocaleString()} ₽
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              {carDetails.priceJPY.toLocaleString()} ¥
            </Typography>

            <Box sx={{ my: 2 }}>
              <Chip
                icon={<CheckCircleIcon />}
                label={`Аукцион ${carDetails.auctionGrade}`}
                color="success"
                sx={{ mr: 1 }}
              />
              <Chip label={`${carDetails.year} год`} sx={{ mr: 1 }} />
              <Chip label={`${carDetails.mileage.toLocaleString()} км`} />
            </Box>

            <List>
              {carDetails.features.map((feature, index) => (
                <ListItem key={index}>
                  <ListItemText primary={feature} />
                </ListItem>
              ))}
            </List>
          </Paper>

          {/* Price Calculation */}
          <Paper sx={{ p: 3, mb: 3, borderRadius: 4 }}>
            <Typography variant="h6" gutterBottom>
              Расчет стоимости
            </Typography>
            <List>
              <ListItem>
                <ListItemText primary="Стоимость автомобиля" />
                <Typography>{carDetails.priceRUB.toLocaleString()} ₽</Typography>
              </ListItem>
              <ListItem>
                <ListItemText primary="Доставка" />
                <Typography>150,000 ₽</Typography>
              </ListItem>
              <ListItem>
                <ListItemText primary="Таможенные платежи" />
                <Typography>200,000 ₽</Typography>
              </ListItem>
              <Divider />
              <ListItem>
                <ListItemText primary="Итого" />
                <Typography variant="h6" color="primary">
                  {calculateTotalPrice().toLocaleString()} ₽
                </Typography>
              </ListItem>
            </List>
          </Paper>

          {/* Contact Form */}
          <Paper sx={{ p: 3, borderRadius: 4 }}>
            <Typography variant="h6" gutterBottom>
              Заказать консультацию
            </Typography>
            <Box component="form" sx={{ mt: 2 }}>
              <TextField
                fullWidth
                label="Ваше имя"
                value={name}
                onChange={(e) => setName(e.target.value)}
                sx={{ mb: 2 }}
              />
              <TextField
                fullWidth
                label="Телефон"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                sx={{ mb: 2 }}
              />
              <TextField
                fullWidth
                label="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                sx={{ mb: 2 }}
              />
              <Button
                variant="contained"
                fullWidth
                size="large"
                startIcon={<LocalShippingIcon />}
              >
                Заказать автомобиль
              </Button>
            </Box>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default CarDetailsPage; 